@import "../../theme/media.scss";

.container {
  align-items: center;
  display: flex;
  flex-direction: column;
  margin: 30px 70px;

  @include for-tiny-screens {
    margin: 30px 20px;
  }

  > div {
    > p {
      font-size: 20px;
      line-height: 30px;
      margin-bottom: 20px;
      text-align: justify;
  
      @include for-tiny-screens {
        font-size: 18px;
        line-height: 25px;
      }
    }
    
    > div {
       margin: auto;
       margin-bottom: 20px;
       max-width: 800px;
    }
  }

}